.work-cover {
  height: 100vh;
  margin-top: 0 !important;
  background-position-x: left;
  position: relative; }

.single-work-tags {
  padding-top: 50px; }

.single-work-tags-wrap {
  display: inline-grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100%;

  h6 {
    font-size: 14px;
    color: #222;
    font-weight: 500;
    margin-right: 10px; }

  .tag-item {
    display: flex; }

  ul {
    list-style: none;
    padding: 0;
    display: flex;
    align-items: center;

    li {
      color: #999;
      font-size: 14px;
      font-weight: 500; } }

  li {
    &:after {
      content: '•';
      margin-left: 5px;
      margin-right: 5px; }

    &:last-child:after {
      content: ""; } } }

.single-work-title {
  font-size: 86px;
  font-weight: 700; }

.challenge-title {
  font-size: 52px;
  font-weight: 700; }

.challenge-text {
  font-size: 20px; }

.s-challenge-line {
  width: 100%;
  position: relative;
  height: 2px;
  background: #222;
  margin-bottom: 70px; }

.s-single-work-title {
  padding: 80px 0; }

.s-challenge {
  padding-bottom: 80px; }

.s-challenge-grid {
  display: inline-grid;
  grid-template-columns: 1fr 2fr; }

.single-work-link-wrap {
  margin-top: 40px;
  display: flex;
  align-items: center; }

.single-work-arrow {
  width: 13px;
  position: relative;
  left: 10px;
  top: 2px;
  transform: rotate(-45deg);
  transition: transform .3s ease; }

.single-work-link-wrap:hover .single-work-arrow {
  transform: rotate(0); }

