.section-review {
  padding: 150px 0; }

.review-block {
  position: relative;
  width: 90%;
  margin: 0 auto; }

.review-quotes {
  position: absolute;
  transform: translate(-100%, -100%); }

.review-content {
  font-size: 38px;
  font-weight: 700;
  line-height: 1.5; }

.review-company-block {
  margin-top: 70px; }

.review-author-name {
  font-size: 24px;
  font-weight: 700; }

.review-company-name {
  font-size: 16px;
  color: $grey-color; }



//related works block

.related-works-link-block {
  display: flex;
  align-items: center;
  img {
    transform: translateX(10px);
    width: 15px; } }

.related-works-link_more {
  font-size: 16px;
  font-weight: 700; }

.related-works-title-block {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.related-works-grid {
  margin-top: 80px;
  display: grid;
  grid-template-columns: 31% 31% 31%;
  column-gap: 3%; }

.related-works-section {
  // padding-top: 100px
  padding-bottom: 80px; }



.cover-block {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  display: block; }

.cover-image {
  object-fit: cover;
  display: block;
  width: 100%;
  height: 100%; }

.related-works-grid .grid-item:first-child .work-item {
  transform: translateY(0); }
